import React from 'react';

const ModalComponent = ({ step, onNext, onPrev, onClose, maxSteps }) => {
  const content = getContentForStep(step);

  const imageStyle = {
    height: step === maxSteps ? 120 : 250,
  };
    
  return (
    <div className="modal">
      {/* Contenu spcifique  chaque tape */}
      <h3>{content.title}</h3>
      <div>
          <img src={content.image} alt={`tape ${step}`} style={imageStyle} />
      </div>
      <div dangerouslySetInnerHTML={{ __html: content.htmlCode }} />
      <div className="boutons">
	  <button onClick={onPrev} disabled={step === 1}>Prcdent</button>
	  <button onClick={onNext} disabled={step === maxSteps}>Suivant</button>
	  {/* Bouton Fermer */}
	  <button onClick={onClose}>Fermer</button>
      </div>
    </div>
  );
};

const getContentForStep = (step) => {
  switch (step) {
    case 1:
      return {
        title: 'Donner un titre',
        image: process.env.PUBLIC_URL + '/h1.png',
        htmlCode: "<div><p>Te voil dsormais plong dans l'univers du HTML. <br />L'idal pour commencer ? Donner un titre ! Cela se fait de la manire suivante : &lt;h1&gt;Texte  afficher&lt;/h1&gt;. <br />Essaie de le faire par toi-mme en tant original !</p></div>",
      };
    case 2:
      return {
        title: 'Ajouter une image',
        image: process.env.PUBLIC_URL + '/img.png',
        htmlCode: "<div><p>Aprs avoir ajout un titre, pourquoi ne pas utiliser une image ? Dans une page web il est trs frquent de voir cela. <br />Pour mettre une image il faut la balise img, le lien de l'image, une description et ventuellement une taille !<br />Je te laisse observer l'exemple ci-dessus (et un lien valide) pour comprendre ce qu'il faut faire ! N'hsite pas  modifier la taille !</p></div>",
      };
    case 3:
      return {
        title: 'Crer un tableau',
        image: process.env.PUBLIC_URL + '/tab.png',
        htmlCode: "<div><p>Maintenant que ces deux composants sont cres et que tu peux observer le rsultat de ton travail, faisons un tableau simple dans lequel tu mettras les informations que tu voudras. <br />La structure sera toujours la mme : table permet de crer un tableau, tr permet d'avoir une ligne de tableau, th correspond aux noms des colonnes et td permet de rentrer les donnes correspondantes. Comme depuis le dbut, n'hsite pas  tre original ! </p></div>",
      };
    case 4:
      return {
        title: 'Ajouter de la couleur',
        image: process.env.PUBLIC_URL + '/css.png',
        htmlCode: "<div><p>Pas mal du tout ! Mais il manque quelque chose n'est-ce pas ? La couleur et le style en gnral ! Ci-dessus tu as des exemples pour modifier certaines proprits de style. La seule limite que tu as pour cette partie est ta crativit : sois curieux comme on le demande  42 et essaie diffrentes proprits, la seule chose  savoir est que toute les balises html (table, h1, etc...) peuvent avoir un style diffrent de celui par dfaut !</p></div>",
      };
  case 5:
      return {
	  title: 'Suis nous sur les rseaux !',
	  image: process.env.PUBLIC_URL + '/logo-42LH.png',
	  htmlCode: `<div><p>Si tu as apprci cet atelier n'hsite pas ! 42 Le Havre est fait pour toi, viens tenter ta chance pour vivre cette magnifique aventure ! <br /> N'hsite pas  nous suivre sur les rseaux ! <br /><div class='logos'><a href='https://www.instagram.com/42_lehavre/'><img src="${process.env.PUBLIC_URL}/insta.png" alt='Instagram' height=80 /></a><a href='https://www.tiktok.com/@42lehavre'><img src="${process.env.PUBLIC_URL}/tiktok.png" alt='Tiktok' height=80 /></a><a href='https://www.facebook.com/42LeHavre'><img src="${process.env.PUBLIC_URL}/facebook.png" alt='Facebook' height=80 /></a><a href='https://www.linkedin.com/school/42-le-havre/'><img src="${process.env.PUBLIC_URL}/linkedin.png" alt='Linkedin' height=80 /></a></div></p></div>`,
      };

    default:
      return {};
  }
};

export default ModalComponent;
